@import url(https://fonts.googleapis.com/css?family=Playfair+Display);
.signature__container {
    margin-top: 25px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.signature__container,
.signature__container a {
    color: #fff;
}

.signature__text,
.signature__link {
    font-size: 1.6rem;
}

.signature__link {
    text-decoration: none;
}

.signature__logo--icon {
    margin-left: 7px;
    font-size: 3rem;
    color: #7dd8f7;
    -webkit-animation: react-logo 4s linear infinite;
            animation: react-logo 4s linear infinite;
}

/* Animations */
@-webkit-keyframes react-logo {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
@keyframes react-logo {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
.App {
  font-family: sans-serif;
  background-color: var(--app-background);
  width: 100vw;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  transition: background ease-in-out 300ms;
}

.quote__block {
  background-color: #fff;
  padding: 30px;
  width: 70%;
  max-width: 700px;
  border-radius: 5px;
  box-shadow: 0 5px 25px 3px rgba(0,0,0,0.3);
  -webkit-animation: container-fit 200ms ease-out forwards;
          animation: container-fit 200ms ease-out forwards;
  transition: all ease-in-out 500ms;
}

.quote__block .quote__text {
  color: var(--app-background);
  transition: color ease-in-out 350ms;
  margin-bottom: 40px;
  font-family: "Playfair Display", serif;
  font-weight: bold;
  font-size: 2.6rem;
  line-height: 1.4;
}

.quote__block .quote__author {
  color: var(--app-background);
  transition: color ease-in-out 350ms;
  font-weight: normal;
  font-size: 1.5rem;
  text-align: right;
}

.quote__block .quote__controls {
  margin-top: 20px;
  height: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.quote__block .quote__controls--button {
  background-color: var(--app-background);
  transition: background ease-in-out 300ms;
  color: #fff;
  border: none;
  border-radius: 3px;
  padding: 0 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 1.2rem;
  text-decoration: none;
  cursor: pointer;
  outline: none;
}

/* Animations */
@-webkit-keyframes container-fit {
  from {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes container-fit {
  from {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
html,
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 10px;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

